function productCardSliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".product-card__slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)

        SlidersArray[index] = new Swiper(`.product-card__slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            nested: true,

            pagination: {
                el: `.product-card__slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
            breakpoints: {
                320: {
                    effect: 'slide',
                },
                992: {
                    effect: 'fade',
                    fadeEffect: {
                        crossFade: true
                    },
                }
            }
        })

        $(`.product-card__slider[data-slider-id="${index}"] .swiper-pagination-bullet`).hover(function () {
            $(this).click()
        })
    })


}

function mainBannerSlider() {
    var swiper = new Swiper('.main-banners .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        autoplay: {
            disableOnInteraction: false,
            pauseOnMouseEnter: true,
        },
        navigation: {
            nextEl: `.main-banners .swiper-button-next`,
            prevEl: `.main-banners .swiper-button-prev`,
        },
        pagination: {
            el: '.main-banners .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function gallery() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            spaceBetween: 0,
            slidesPerView: "auto",
            freeMode: true,
            watchSlidesProgress: true,
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            spaceBetween: 0,
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            },
            thumbs: {
                swiper:  gallery.mySwiper,
            },
        });

        SlidersArray[index] = gallery
    })

}

Sliders()
mainBannerSlider()
productSliders()
productCardSliders()
gallery()
