$(".burger").click(function () {
    $(".header").addClass("_open-burger-menu")
    $("body").addClass("_fixed-body")
})

$(".mob-menu__close").click(function () {
    $(".header").removeClass("_open-burger-menu")
    $("body").removeClass("_fixed-body")
})

$(".catalog-btn").click(function () {
    $(".header").toggleClass("_open-catalog-menu")
    if(innerWidth<=992){
        $("body").addClass("_fixed-body")
    }
})

$(".brand-btn").click(function () {
    $(".header").toggleClass("_open-brand-menu")
    if(innerWidth<=992){
        $("body").addClass("_fixed-body")
    }
})

$(".sale-btn").click(function () {
    $(".header").toggleClass("_open-sale-menu")
    if(innerWidth<=992){
        $("body").addClass("_fixed-body")
    }
})

$(".main").click(function () {
    $(".header").removeClass("_open-catalog-menu")
    $(".header").removeClass("_open-brand-menu")
    $(".header").removeClass("_open-sale-menu")
})

$(document).mouseup( function(e){
    let div = $( ".header" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        $(div).removeClass("_open-catalog-menu")
        $(div).removeClass("_open-brand-menu")
        $(div).removeClass("_open-sale-menu")
    }
});

$(".menu-item__show").click(function () {
    $(".menu-item2").removeClass("_open-menu3")
    $(".menu-item").removeClass("_open-menu2")
    $(this).parents(".menu-item").addClass("_open-menu2")
})

$(".menu-item2__show").click(function () {
    $(".menu-item2").removeClass("_open-menu3")
    $(this).parents(".menu-item2").addClass("_open-menu3")
})

$(".menu2 > .menu-top .menu-top__back").click(function () {
    $(".menu-item").removeClass("_open-menu2")
})

$(".menu3 > .menu-top .menu-top__back").click(function () {
    $(".menu-item2").removeClass("_open-menu3")
})

$(".menu-top__close").click(function () {
    $(".menu-item").removeClass("_open-menu2")
    $(".menu-item2").removeClass("_open-menu3")
    $(".header").removeClass("_open-catalog-menu")
    $("body").removeClass("_fixed-body")
})

$(".header-search__field input").on("input",function () {
    let val = $(this).val()
    if(val.length > 4) {
        $(".header-search").addClass("_open")
    } else{
        $(".header-search").removeClass("_open")
    }
})


$(document).mouseup( function(e){
    let div = $( ".header-search" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});

$(".city-modal__item").click(function () {
    setCity(this)
})

function setCity(el) {
    let cityName = $(el).attr("data-city")
    $(".header-city__title").html(cityName)
    $(".modal").removeClass("_active")
    $("body").removeClass("fixed-body2")
}

$("._close-city-hint").click(function () {
    $(".city-hint").removeClass("_active")
})
