function compareSlider() {
    var swiper = new Swiper(".compare-slider .swiper-container", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
    });
    var swiper2 = new Swiper(".compare-slider2 .swiper-container", {
        spaceBetween: 0,
        slidesPerView: "auto",
        navigation: {
            nextEl: `.compare .swiper-button-next`,
            prevEl: `.compare .swiper-button-prev`,
        },
    });

    swiper.on('slideChange', function () {
        swiper2.slideTo(swiper.activeIndex)
    });
    swiper2.on('slideChange', function () {
        swiper.slideTo(swiper2.activeIndex)
    });
}



compareSlider()


let card = document.querySelector(".compare-card__inner")

$(window).scroll(function(){
    showCompareHeader()
});
$(window).resize(function(){
    showCompareHeader()
});


showCompareHeader()


function showCompareHeader() {
    if(!card){
        return
    }


    if(card.getBoundingClientRect().top < (-1 * card.getBoundingClientRect().height * .7)){
        $(".compare-header").addClass("_transform")
    } else{
        $(".compare-header").removeClass("_transform")
    }
}

